.arc {
  --arc-color-transparent: transparent;
  --arc-color-text-dark: #353535;
  --arc-color-text-inactive: #c9c9ca;
  --arc-color-text-light: #fff;
  --arc-color-bg-light: #fff;
  --arc-color-border: #f3f3f3;
  --arc-color-text-hover: #f8f9fa;
  --arc-color-primary: rgb(28 126 214);
  --arc-color-primary-light: rgba(28 126 214/40%);
  --arc-color-primary-lighter: rgba(28 126 214/10%);
  --arc-color-accent: rgb(240 62 62);
  --arc-color-accent-light: rgba(240 62 62/48%);
  --arc-color-accent-lighter: rgba(240 62 62/7%);
  --arc-color-border-root: var(--arc-color-border);
  --arc-color-bg-text-hover-header-button: var(--arc-color-text-hover);
  --arc-color-text-today: var(--arc-color-primary);
  --arc-color-border-weekdays: var(--arc-color-border);
  --arc-color-text-weekday-label: var(--arc-color-text-inactive);
  --arc-color-text-weekend-label: var(--arc-color-accent-light);
  --arc-color-text-dom-inactive: var(--arc-color-text-inactive);
  --arc-color-text-dom-active: var(--arc-color-text-dark);
  --arc-color-text-dom-weekend-active: var(--arc-color-accent);
  --arc-color-text-dom-weekend-inactive: var(--arc-color-accent-light);
  --arc-color-bg-dom-in-range-active: var(--arc-color-primary-lighter);
  --arc-color-bg-dom-in-range-inactive: var(--arc-color-primary-lighter);
  --arc-color-bg-dom-in-range-weekend-active: var(--arc-color-accent-lighter);
  --arc-color-bg-dom-range-ep-active: var(--arc-color-primary);
  --arc-color-bg-dom-range-ep-inactive: var(--arc-color-primary-lighter);
  --arc-color-bg-dom-range-ep-weekend-active: var(--arc-color-accent);
  --arc-color-bg-dom-range-ep-weekend-inactive: var(--arc-color-accent-lighter);
  --arc-color-text-dom-range-ep-weekend-inactive: var(--arc-color-accent-light);
  --arc-color-text-dom-range-ep-active: var(--arc-color-text-light);
  --arc-color-text-dom-range-ep-inactive: var(--arc-color-primary-light);
  --arc-color-bg-dom-selected: var(--arc-color-primary);
  --arc-color-bg-dom-weekend-selected: var(--arc-color-accent);
  --arc-color-text-dom-selected: var(--arc-color-text-light);
  --arc-color-text-dom-weekend-selected: var(--arc-color-text-light);
  --arc-color-text-disabled: var(--arc-color-text-inactive);
  --arc-color-text-disabled-weekend: var(--arc-color-accent-light);
  --arc-color-bg-disabled: var(--arc-color-bg-light);
  --arc-color-bg-disabled-cross: var(--arc-color-text-inactive);
  --arc-color-bg-disabled-weekend-cross: var(--arc-color-accent-light);
}
.arc.dark {
  --arc-color-transparent: transparent;
  --arc-color-text-dark: #fff;
  --arc-color-text-inactive: #727273;
  --arc-color-text-light: #353535;
  --arc-color-bg-light: #353535;
  --arc-color-border: #4e4d4d;
  --arc-color-text-hover: #403e3e;
  --arc-color-primary: #ced9ea;
  --arc-color-primary-light: #6d707f;
  --arc-color-primary-lighter: #474b52;
  --arc-color-accent: #eacece;
  --arc-color-accent-light: #7f6d6d;
  --arc-color-accent-lighter: #524747;
}
.arc {
  border: 1px solid var(--arc-color-border-root);
  border-radius: 8px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans,
    Helvetica Neue, sans-serif;
  font-size: 1rem;
  background-color: var(--arc-color-bg-light);
}
.arc * {
  box-sizing: border-box;
  -webkit-tap-highlight-color: var(--arc-color-transparent);
  font-family: inherit;
}
.arc button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}
.arc button::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.arc_header_label,
.arc_header_nav {
  border-radius: 6px;
  cursor: pointer;
}
.arc_header_label div + div {
  margin-left: 2.5%;
}
.arc_header span {
  font-size: 0.84em;
  color: var(--arc-color-text-dark);
}
.arc_header button:hover {
  background-color: var(--arc-color-bg-text-hover-header-button);
}
.arc_header button:active {
  transform: translateY(1px);
}
.arc_view {
  width: 100%;
}
.arc_view-months .arc_view_cell button {
  font-size: 0.83em;
  color: var(--arc-color-text-dom-active);
  cursor: pointer;
}
.arc_view-months .arc_view_cell button:hover {
  background-color: var(--arc-color-bg-text-hover-header-button);
}
.arc_view-months .arc_view_cell.arc_this_month {
  border-radius: 6px;
  color: var(--arc-color-text-today);
}
.arc_view-years .arc_view_cell button {
  font-size: 0.83em;
  color: var(--arc-color-text-dom-active);
  cursor: pointer;
}
.arc_view-years .arc_view_cell button:hover {
  background-color: var(--arc-color-bg-text-hover-header-button);
}
.arc_view-years .arc_view_cell.arc_this_year {
  border-radius: 6px;
  color: var(--arc-color-text-today);
}
.arc_view_weekdays {
  border-bottom: 1px solid var(--arc-color-border-weekdays);
}
.arc_view_weekdays_cell {
  color: var(--arc-color-text-weekday-label);
  font-size: 0.83em;
  text-transform: uppercase;
}
.arc_view_weekdays_cell.arc_wknd {
  color: var(--arc-color-text-weekend-label);
}
.arc_view-days-of-month .arc_view_cell {
  cursor: pointer;
}
.arc_view-days-of-month .arc_view_cell .arc_view_cell_value {
  border-radius: 2px;
  cursor: pointer;
}
.arc_view-days-of-month .arc_view_cell .arc_view_cell_value:hover {
  background-color: var(--arc-color-bg-text-hover-header-button);
}
.arc_view-days-of-month .arc_view_cell button {
  font-size: 0.83em;
  color: var(--arc-color-text-dom-inactive);
  cursor: pointer;
}
.arc_view-days-of-month .arc_view_cell.arc_active button {
  color: var(--arc-color-text-dom-active);
}
.arc_view-days-of-month .arc_view_cell.arc_wknd button {
  color: var(--arc-color-text-dom-weekend-active);
}
.arc_view-days-of-month .arc_view_cell.arc_wknd:not(.arc_active) button {
  color: var(--arc-color-text-dom-weekend-inactive);
}
.arc_view-days-of-month .arc_view_cell.arc_in_range.arc_active .arc_view_cell_value {
  background-color: var(--arc-color-bg-dom-in-range-active);
  color: var(--arc-color-text-dark);
}
.arc_view-days-of-month .arc_view_cell.arc_in_range.arc_wknd.arc_active .arc_view_cell_value {
  background-color: var(--arc-color-bg-dom-in-range-weekend-active);
  color: var(--arc-color-text-dark);
}
.arc_view-days-of-month .arc_view_cell.arc_range_end.arc_active button,
.arc_view-days-of-month .arc_view_cell.arc_range_start.arc_active button {
  color: var(--arc-color-text-dom-range-ep-active);
}
.arc_view-days-of-month .arc_view_cell.arc_range_end.arc_active .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_range_start.arc_active .arc_view_cell_value {
  background-color: var(--arc-color-bg-dom-range-ep-active);
}
.arc_view-days-of-month .arc_view_cell.arc_range_end.arc_wknd.arc_active .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_range_start.arc_wknd.arc_active .arc_view_cell_value {
  background-color: var(--arc-color-bg-dom-range-ep-weekend-active);
}
.arc_view-days-of-month .arc_view_cell.arc_range_end.arc_wknd.arc_active button,
.arc_view-days-of-month .arc_view_cell.arc_range_start.arc_wknd.arc_active button {
  border-bottom-color: var(--arc-color-bg-dom-range-ep-weekend-active);
}
.arc_view-days-of-month .arc_view_cell.arc_range_end:not(.arc_active) .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_range_start:not(.arc_active) .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_selected:not(.arc_active) .arc_view_cell_value {
  background-color: var(--arc-color-bg-dom-range-ep-inactive);
}
.arc_view-days-of-month .arc_view_cell.arc_range_end:not(.arc_active) button,
.arc_view-days-of-month .arc_view_cell.arc_range_start:not(.arc_active) button,
.arc_view-days-of-month .arc_view_cell.arc_selected:not(.arc_active) button {
  color: var(--arc-color-text-dom-range-ep-inactive);
}
.arc_view-days-of-month .arc_view_cell.arc_in_range.arc_range_end.arc_wknd:not(.arc_active) .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_in_range.arc_range_start.arc_wknd:not(.arc_active) .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_selected.arc_wknd:not(.arc_active) .arc_view_cell_value {
  background-color: var(--arc-color-bg-dom-range-ep-weekend-inactive);
}
.arc_view-days-of-month .arc_view_cell.arc_in_range.arc_range_end.arc_wknd:not(.arc_active) button,
.arc_view-days-of-month .arc_view_cell.arc_in_range.arc_range_start.arc_wknd:not(.arc_active) button,
.arc_view-days-of-month .arc_view_cell.arc_selected.arc_wknd:not(.arc_active) button {
  color: var(--arc-color-text-dom-range-ep-weekend-inactive);
}
.arc_view-days-of-month .arc_view_cell.arc_highlight:not(.arc_selected):not(.arc_disabled) button,
.arc_view-days-of-month .arc_view_cell.arc_today:not(.arc_selected):not(.arc_disabled) button {
  border-bottom: 1px solid;
}
.arc_view-days-of-month .arc_view_cell.arc_selected button:focus {
  outline: none;
}
.arc_view-days-of-month .arc_view_cell.arc_selected .arc_view_cell_value {
  background-color: var(--arc-color-bg-dom-selected);
}
.arc_view-days-of-month .arc_view_cell.arc_selected button {
  color: var(--arc-color-text-dom-selected);
}
.arc_view-days-of-month .arc_view_cell.arc_selected.arc_wknd .arc_view_cell_value {
  background-color: var(--arc-color-bg-dom-weekend-selected);
}
.arc_view-days-of-month .arc_view_cell.arc_selected.arc_wknd button {
  color: var(--arc-color-text-dom-weekend-selected);
}
.arc_view-days-of-month .arc_view_cell.arc_disabled {
  position: relative;
}
.arc_view-days-of-month .arc_view_cell.arc_disabled.arc_in_range .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_disabled.arc_in_range.arc_wknd .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_disabled .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_disabled.arc_wknd .arc_view_cell_value {
  background-color: var(--arc-color-bg-disabled);
}
.arc_view-days-of-month .arc_view_cell.arc_disabled button {
  color: var(--arc-color-text-disabled);
  cursor: none;
  pointer-events: none;
}
.arc_view-days-of-month .arc_view_cell.arc_disabled.arc_wknd button {
  color: var(--arc-color-text-disabled-weekend);
}
.arc_view-days-of-month .arc_view_cell.arc_disabled button:before {
  content: '';
  position: absolute;
  top: 15%;
  left: 50%;
  transform: rotate(45deg);
  font-weight: 300;
  display: block;
  height: 70%;
  background-color: var(--arc-color-bg-disabled-cross);
  width: 1px;
}
.arc_view-days-of-month .arc_view_cell.arc_disabled.arc_wknd button:before,
.arc_view-days-of-month .arc_view_cell.arc_wknd button:before {
  background-color: var(--arc-color-bg-disabled-weekend-cross);
}
